/*css reset*/
* {
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-family: Arial, Verdana;
    word-break: break-all;
}
@font-face {
    font-family: 'SourceHanSansSC-Regular';
    src: url(../../static/fonts/SourceHanSansK-Regular-subfont.64ac65e.ttf);
}
@font-face {
    font-family: 'SourceHanSansSC-Bold';
    src: url(../../static/fonts/SourceHanSansK-Bold-subfont.a872111.ttf);
}
@font-face {
    font-family: 'SourceHanSansSC-Heavy';
    src: url(../../static/fonts/SourceHanSansK-Heavy-subfont.64ddf60.ttf);
}
@font-face {
    font-family: 'SourceHanSansSC-Medium';
    src: url(../../static/fonts/SourceHanSansK-Medium-subfont.f4ed255.ttf);
}
@font-face {
    font-family: 'LeagueGothic-Regular';
    src: url(data:application/x-font-ttf;base64,);
}
html,
body {
    margin: 0;
